  import Citys from './citys'
  export default {
    props: {
      hotCitys: {
        type: Array,
        default () {
          return []
        }
      },
      value: {
        type: String,
        default: '未设置'
      }
    },
    data() {
      return {
				focus:false,
        citys: Citys,
        windowHeight: '',
        scrollIntoId: 'F',
        current: this.value,
				search: ''
      }
    },
    mounted() {
      this.getSystemInfo()
    },
		watch:{
			value(val) {
				this.current = val;
			} 
		},
    methods: {
      getSystemInfo() {
        uni.getSystemInfo().then(res => {
          let [error, data] = res
          this.windowHeight = `${data.windowHeight}px`
        })
      },
      scrollTo(letter) {
        this.scrollIntoId = letter === '#' ? 'current' : letter
      },
      onSelect(city) {
        this.current = city
        this.$emit('input', city)
        this.$emit('on-select', city)
      },
			historyCityt(city){
				this.current = city;
				this.$emit('searchCity', city)
			},
			clearHistory(){
				this.$emit("clearHistory");
			},
			position(){
				this.$emit("position");
				this.current = this.value;
				console.log(this.value);
			},
			searchCity() {
				this.$emit("searchCity",this.search);
			}
    }
  }
